<template>
  <section id="direct">
    <loading :active.sync="isLoading" :can-cancel="true" :is-full-page="fullPage"></loading>
    <div class="container" v-if="!!currentCategory">
      <div class="mt-2">
        <h1 class="pl-2">{{$route.params.category | capitalize}}</h1>
        <div class="d-flex justify-content-between">
          <div class="card result-card px-md-3 py-md-2">
            <div
              class="d-flex border-bottom game-type d-flex justify-content-between align-items-center border-bottom direct-bet"
            >
              <div class="justify-content-start">
                <p>
                  <span>1-5 Direct Bet</span>
                </p>
              </div>
              <div class="d-flex justify-content-end align-items-center">
                <b-tabs class="d-inline-flex my-4" v-model="tabIndex">
                  <b-tab title="Direct 1" :title-link-class="linkClass(0)" @click="numberLimit(1)"></b-tab>
                  <b-tab title="Direct 2" :title-link-class="linkClass(1)" @click="numberLimit(2)"></b-tab>
                  <b-tab title="Direct 3" :title-link-class="linkClass(2)" @click="numberLimit(3)"></b-tab>
                  <b-tab title="Direct 4" :title-link-class="linkClass(3)" @click="numberLimit(4)"></b-tab>
                  <b-tab title="Direct 5" :title-link-class="linkClass(4)" @click="numberLimit(5)"></b-tab>
                </b-tabs>
              </div>
            </div>
            <div class="pb-md-5">
              <div class="d-flex flex-wrap">
                <div class="mt-3 pl-2 ball-cover" v-for="i in 90" :key="i">
                  <div
                    class="ball d-flex align-items-center justify-content-center add_cursor"
                    @click.prevent="addNumber(i,$event)"
                  >
                    <div class="white-bg d-flex align-items-center justify-content-center">{{i}}</div>
                  </div>
                  <img class="shadow" src="/img/shadow.png">
                </div>
              </div>

              <div class="pt-2 pb-3 border-bottom">
                <h6>
                  You Selected
                  <span class="play ml-2 px-2 py-1">{{selected.length}}</span> Numbers
                </h6>
                <div class="card card-result py-2 pl-2 redirect">
                  <div class="ball addmargin select-ball" v-for="select in selected" :key="select">
                    <div
                      class="white-bg1 d-flex align-items-center justify-content-center"
                    >{{select}}</div>
                    <img class="shadow1" src="/img/shadow.png">
                  </div>
                </div>
              </div>
            </div>
            <div class="container pay mb-3">
              <div class="row">
                <div class="col-7 justify-content-start">
                  <input
                    type="text"
                    v-model="amount"
                    placeholder="Line Stake"
                    class="card-result pt-2 mr-3 d-inline"
                  >
                  <router-link to="/wallet">
                    <p class="how-to-play d-inline">Fund Wallet?</p>
                  </router-link>
                  <span
                    class="badge badge-success balance2"
                  >{{userBalance.currency}}{{commafy(userBalance.amount)}}</span>
                </div>
                <div class="col-5">
                  <div class="d-flex align-items-center">
                    <button @click.prevent="reset()" class="btn play2 btn-lg py-2 px-4" mr-3>Reset</button>
                    <button
                      :disabled="!selected.length ||  this.amount < 5 "
                      @click.prevent="submit()"
                      class="btn play3 btn-lg py-2 px-4"
                    >Submit</button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="container" v-else-if="!!currentCategory">
      <div class="mt-2">
        <h1 class="pl-2">{{$route.params.category | capitalize}}</h1>
        <div class="d-flex justify-content-between">
          <div class="card result-card px-md-3 py-md-2">
            <div
              class="d-flex border-bottom game-type d-flex justify-content-between align-items-center border-bottom direct-bet"
            >
              <div class="justify-content-start">
                <p>
                  <span>1-5 Direct Bet</span>
                </p>
              </div>
              <div class="d-flex justify-content-end align-items-center">
                <b-tabs class="d-inline-flex my-4" v-model="tabIndex">
                  <b-tab title="Direct 1" :title-link-class="linkClass(0)" @click="numberLimit(1)"></b-tab>
                  <b-tab title="Direct 2" :title-link-class="linkClass(1)" @click="numberLimit(2)"></b-tab>
                  <b-tab title="Direct 3" :title-link-class="linkClass(2)" @click="numberLimit(3)"></b-tab>
                  <b-tab title="Direct 4" :title-link-class="linkClass(3)" @click="numberLimit(4)"></b-tab>
                  <b-tab title="Direct 5" :title-link-class="linkClass(4)" @click="numberLimit(5)"></b-tab>
                </b-tabs>
              </div>
            </div>
            <div class="pb-md-5">
              <div class="d-flex flex-wrap">
                <div class="mt-3 pl-2 ball-cover" v-for="i in 90" :key="i">
                  <div
                    class="ball d-flex align-items-center justify-content-center add_cursor"
                    @click.prevent="addNumber(i,$event)"
                  >
                    <div class="white-bg d-flex align-items-center justify-content-center">{{i}}</div>
                  </div>
                  <img class="shadow" src="/img/shadow.png">
                </div>
              </div>

              <div class="pt-2 pb-3 border-bottom">
                <h6>
                  You Selected
                  <span class="play ml-2 px-2 py-1">{{selected.length}}</span> Numbers
                </h6>
                <div class="card card-result py-2 pl-2 redirect">
                  <div class="ball addmargin select-ball" v-for="select in selected" :key="select">
                    <div
                      class="white-bg1 d-flex align-items-center justify-content-center"
                    >{{select}}</div>
                    <img class="shadow1" src="/img/shadow.png">
                  </div>
                </div>
              </div>
            </div>
            <div class="container pay mb-3">
              <div class="row">
                <div class="col-7 justify-content-start">
                  <input
                    type="text"
                    v-model="amount"
                    placeholder="Line Stake"
                    class="card-result pt-2 mr-3 d-inline"
                  >
                  <router-link to="/wallet">
                    <p class="how-to-play d-inline">Fund Wallet?</p>
                  </router-link>
                  <span
                    class="badge badge-success balance2"
                  >{{userBalance.currency}}{{commafy(userBalance.amount)}}</span>
                </div>
                <div class="col-5">
                  <div class="d-flex align-items-center">
                    <button @click.prevent="reset()" class="btn play2 btn-lg py-2 px-4" mr-3>Reset</button>
                    <button
                      :disabled="!selected.length ||  this.amount < 5 "
                      @click.prevent="submit()"
                      class="btn play3 btn-lg py-2 px-4"
                    >Submit</button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="d-flex justify-content-center align-items-center" style="height:100px;" v-else>
      <h1>Game currently unavailable</h1>
    </div>
    <Modal v-show="showModal">
      <div class="modal-header">
        <h1>Ticket</h1>
      </div>
      <template>
        <div style="position:relative;" v-for="(game,index) in currentgame" :key="index">
          <div
            class="close_icon"
            @click.prevent="removeGame({category: $route.params.category, index: index})"
          >
            <i class="fas fa-times"></i>
          </div>
          <template v-if="game.type == 'Banker Bet'">
            <div class="modal-body">
              <p>You are playing Banker Bet</p>
            </div>
            <div class="modal-footer" style="display: block;border-bottom: 1px solid #dee2e6;">
              <p class="m-0">Your Selected {{game.selection1.length}} Numbers</p>
              <div class="card card-result py-2 pl-2 redirect redirect2">
                <div
                  class="ball addmargin selected-balls"
                  v-for="select1 in game.selection1"
                  :key="select1"
                >
                  <div
                    class="white-bg1 d-flex align-items-center justify-content-center"
                  >{{select1}}</div>
                  <img class="shadow1" src="/img/shadow.png">
                </div>
              </div>
              <p class="m-0">Your Selected {{game.selection2.length}} Numbers</p>
              <div class="card card-result py-2 pl-2 redirect redirect2">
                <div
                  class="ball addmargin selected-balls"
                  v-for="select2 in game.selection2"
                  :key="select2"
                >
                  <div
                    class="white-bg1 d-flex align-items-center justify-content-center"
                  >{{select2}}</div>
                  <img class="shadow1" src="/img/shadow.png">
                </div>
              </div>
              <p class="mt-5 mb-0">Amount</p>
              <h3>{{userBalance.currency}}{{commafy(game.total_stake)}}</h3>
            </div>
          </template>
          <template v-else>
            <div class="pt-0 pb-0 modal-body">
              <p>{{game.type}}</p>
            </div>

            <div
              class="pt-0 modal-footer"
              style="display: block; border-top: none; border-bottom: 1px solid #dee2e6;"
            >
              <p class="m-0">Your Selected Numbers</p>
              <div class="card card-result py-2 pl-2 redirect redirect2">
                <div
                  class="ball addmargin selected-balls"
                  v-for="select in game.selection"
                  :key="select"
                >
                  <div class="white-bg1 d-flex align-items-center justify-content-center">{{select}}</div>
                  <img class="shadow1" src="/img/shadow.png">
                </div>
              </div>
              <p class="mt-5 mb-0">Amount</p>
              <h3>{{userBalance.currency}}{{commafy(isMachine ? game.total_stake * 2 : game.total_stake)}}</h3>
            </div>
          </template>
        </div>
				<br>
				<div class="mb-3 ml-3">
        <span id="instruction" v-if="currentgame.length > 0">Click to change ticket type</span>
        </div>
					<div class="pb-3  col-6 d-inline-flex justify-content-start" v-if="currentgame.length > 0">
            <input
              class="tgl tgl-flip"
              style="width:200% !important;"
              id="cb5"
              type="checkbox"
              v-model="isMachine"
            >
            <label class="tgl-btn" data-tg-off="Winning Number" data-tg-on="Machine Number" for="cb5"></label>
            <!-- <label class='tgl-btn' for='cb5'></label> -->
          </div>
				<div class="d-flex justify-content-end align-items-center mb-3 mr-3 mobile-preview">
          <div>
            <button
              @click.prevent="cancel(); removeGame({category: $route.params.category, index: index});"
              class="btn cancel btn-lg py-2 px-4 mr-3"
            >Cancel Ticket</button>
            </div>
            <div>
            <button v-if="currentgame.length > 0" @click.prevent="addMore()" class="btn play2 btn-lg py-2 px-4 mr-3">Add More</button>
            </div>
            <div v-if="(this.isMachine ? this.amount.replace(/,/g,'')  * 2 *  currentgame.length : this.amount.replace(/,/g,'') *  currentgame.length ) > userBalance.amount">
            <button v-if="currentgame.length > 0"  class="btn cancel btn-lg py-2 px-4 mr-3">Fund Account</button>
            </div>
            <div v-else>
            <button v-if="currentgame.length > 0" @click.prevent="play() + this.clearTicket(this.$route.params.category);" class="btn play3 btn-lg py-2 px-4 mr-3">Play bet</button>
            </div>
				</div>
      </template>
    </Modal>
  </section>
</template>

<script>
import GameCard from "@/components/GameCard.vue";
import PillsNav from "@/components/PillsNav.vue";
import Modal from "@/components/Modal.vue";
import { mapState, mapActions, mapMutations, mapGetters } from "vuex";
import Loading from "vue-loading-overlay";
import "vue-loading-overlay/dist/vue-loading.css";
export default {
  components: { GameCard, PillsNav, Modal, Loading },
  filters: {
    capitalize: function(value) {
      if (!value) return "";
      value = value.toString();
      return value.charAt(0).toUpperCase() + value.slice(1);
    }
  },
  watch: {
    $route(to, from) {
      this.reset();
    },
    amount: function(newValue) {
      const result = newValue
        .replace(/\D/g, "")
        .replace(/\B(?=(\d{3})+(?!\d))/g, ",");
      this.$nextTick(() => (this.amount = result));
    }
  },
 
  data() {
    return {
      isLoading: false,
      fullPage: true,
      tabIndex: 0,
      isMachine: false,
      details: [
        {
          title: "Naija Gold",
          money: "300, 000",
          last_draw: "05 Mar 2019",
          time: "04:04:04"
        },
        {
          title: "Success",
          money: "450, 347",
          last_draw: "05 Mar 2019",
          time: "01:07:11"
        }
      ],
      selected: [],
      limit: 1,
      showModal: false,
      amount: "",
      market: 771,
      			index: ''

    };
  },
  methods: {
    async play() {
      this.showModal = false;
       this.isLoading = true;
      const games = [];

     this.currentgame.forEach((game) => {
				if (game.type == 'Banker Bet') {
					game.total_stake = this.isMachine ? game.total_stake * 2 : game.total_stake;
          game.selection = this.isMachine ? `(+M)A:${game.selection1.toString()}-B:${game.selection2.toString()}` : `A:${game.selection1.toString()}-B:${game.selection2.toString()}`;
          delete game.selection1;
					delete game.selection2;
				} else {
					game.total_stake = this.isMachine ? game.total_stake * 2 : game.total_stake;
					game.selection = this.isMachine ? `(+M)${game.selection.toString()}` : `${game.selection.toString()}`;
				}
				games.push(game);
			});
       try {
				let res = await this.placeBet(games);
				this.isLoading = false;
					this.$swal({
						type: 'success',
						title: 'Success',
						showConfirmButton: false,
						timer: 3000
				})				
       this.reset();
      this.clearTicket(this.$route.params.category);
				this.$router.push('/dashboard')
			} catch (e) {
				this.isLoading = false;
				 console.log(e);
				this.$swal({
					type: 'error',
					title: 'Unable To Place Bet',
					showConfirmButton: false,
					timer: 3000
        })
         this.reset();
      	this.clearTicket(this.$route.params.category);
				return
			}
			// console.log(res)
			 // simulate AJAX
                // setTimeout(() => {
                //   this.isLoading = false
                // },5000)
								
			// if(res.statusCode.errorCode){
			// 	console.log('error here');
			// }
		},
    ...mapActions(["placeBet"]),
    ...mapMutations([
      "setSuccess",
      "setNaira",
      "setKano",
      "setNaijaGold",
      "setRich",
      "setBonanza",
      "setFortune",
      "setLg",
      "setMidWeek",
      "setMsp",
      "setNational",
      "setCash",
      "setWinna",
      "setMillim",
      "removeGame",
      "updateDisabledState",
      "clearTicket"
    ]),
    submit() {
      if (parseInt(this.amount.replace(/,/g, "")) <= 0) {
        this.$swal({
          type: "error",
          title: `Please input valid amount`,
          showConfirmButton: false,
          timer: 2000
        });
        return;
      }
      this.showModal = true;
      var game = {
        type: `Direct ${this.limit} Bet`,
        id: this.currentCategory.id,
        selection: this.selected,
        lines: 1,
        line_stake: parseInt(this.amount.replace(/,/g, "")),
        total_stake: this.isMachine ? parseInt(this.amount.replace(/,/g, "")) * 2 : parseInt(this.amount.replace(/,/g, "")),
        market: this.market,
        isMachine: this.isMachine
      };
      if (this.$route.params.category == "success") {
        this.setSuccess({ game, category: "success" });
      }
      if (this.$route.params.category == "naira") {
        this.setNaira({ game, category: "naira" });
      }
      if (this.$route.params.category == "kano") {
        this.setKano({ game, category: "kano" });
      }
      if (this.$route.params.category == "naija-gold") {
        this.setNaijaGold({ game, category: "naija_gold" });
      }
      if (this.$route.params.category == "rich") {
        this.setRich({ game, category: "rich" });
      }
      if (this.$route.params.category == "lg") {
        this.setLg({ game, category: "lg" });
      }
      if (this.$route.params.category == "bonanza") {
        this.setBonanza({ game, category: "bonanza" });
      }
      if (this.$route.params.category == "fortune") {
        this.setFortune({ game, category: "fortune" });
      }
      if (this.$route.params.category == "midweek") {
        this.setMidWeek({ game, category: "midweek" });
      }
       if (this.$route.params.category == "national") {
        this.setNational({ game, category: "national" });
      }
       if (this.$route.params.category == "msp") {
        this.setMsp({ game, category: "msp" });
      }
      if (this.$route.params.category == "cash") {
        this.setCash({ game, category: "cash" });
      }
      if (this.$route.params.category == "winna") {
        this.setWinna({ game, category: "winna" });
      }
      if (this.$route.params.category == "millim") {
        this.setMillim({ game, category: "millim" });
      }
    },
    reset() {
      this.market = 771;
      this.limit = 1;
      this.tabIndex = 0;
      this.selected = [];
      this.amount = "";
      var elems = document.querySelectorAll(".select-ball");
      [].forEach.call(elems, function(el) {
        el.classList.remove("select-ball");
      });
    },
    linkClass(idx) {
      if (this.tabIndex === idx) {
        return ["pill-green", "text-white"];
      } else {
        return ["pill-default", "text-black"];
      }
    },
    addNumber(number, $event) {
      if (this.selected.length <= this.limit) {
        if (this.selected.indexOf(number) !== -1) {
          var index = this.selected.indexOf(number);
          this.selected.splice(index, 1);
          $event.target.closest(".ball").classList.toggle("select-ball");
        } else {
          if (this.selected.length < this.limit) {
            this.selected.push(number);
            $event.target.closest(".ball").classList.toggle("select-ball");
          } else {
            this.$swal({
              type: "info",
              title: `You can't select more than ${this.limit} number(s)`,
              showConfirmButton: false,
              timer: 2000
            });
          }
        }
      }
    },
    numberLimit(number) {
      var elems = document.querySelectorAll(".select-ball");
      [].forEach.call(elems, function(el) {
        el.classList.remove("select-ball");
      });
      this.limit = number;
      this.selected = [];
      switch (number) {
        default:
          this.market = 771;
          break;
        case 1:
          this.market = 771;
          break;
        case 2:
          this.market = 772;
          break;
        case 3:
          this.market = 773;
          break;
        case 4:
          this.market = 774;
          break;
        case 5:
          this.market = 775;
      }
    },
    addMore() {
      this.showModal = !this.showModal;
      this.reset();
      if (this.currentgame.length > 0) {
        this.updateDisabledState({
          boolean: true,
          category: this.$route.params.category
        });
      }
    },
    cancel() {
      this.showModal = !this.showModal;
      this.reset();
      	  this.clearTicket(this.$route.params.category);

    },
  },
  computed: {
    ...mapGetters(["commafy"]),
    ...mapState([
      "success",
      "naira",
      "kano",
      "naija_gold",
      "rich",
      "lg",
      "midweek",
      "msp",
      "national",
      "fortune",
      "bonanza",
      "cash",
      "winna",
      "millim",
      "schedule",
      "userBalance"
    ]),
    currentgame() {
      if (this.$route.params.category == "success") {
        return this.success;
      }
      if (this.$route.params.category == "naira") {
        return this.naira;
      }
      if (this.$route.params.category == "kano") {
        return this.kano;
      }
      if (this.$route.params.category == "naija-gold") {
        return this.naija_gold;
      }
      if (this.$route.params.category == "rich") {
        return this.rich;
      }
      if (this.$route.params.category == "lg") {
        return this.lg;
      }
       if (this.$route.params.category == "fortune") {
        return this.fortune;
      }
      if (this.$route.params.category == "bonanza") {
        return this.bonanza;
      }
      if (this.$route.params.category == "midweek") {
        return this.midweek;
      }
       if (this.$route.params.category == "msp") {
        return this.msp;
      }
       if (this.$route.params.category == "national") {
        return this.national;
      }
      if (this.$route.params.category == "cash") {
        return this.cash;
      }
      if (this.$route.params.category == "winna") {
        return this.winna;
      }
      if (this.$route.params.category == "millim") {
        return this.millim;
      }
    },
    currentCategory() {
      if (this.$route.params.category == "success") {
        return this.schedule.find(schel => schel.name == "Success");
      }
      if (this.$route.params.category == "naira") {
        return this.schedule.find(schel => schel.name == "Naira");
      }
      if (this.$route.params.category == "kano") {
        return this.schedule.find(schel => schel.name == "Kano");
      }
      if (this.$route.params.category == "naija-gold") {
        return this.schedule.find(schel => schel.name == "Naija Gold");
      }
      if (this.$route.params.category == "rich") {
        return this.schedule.find(schel => schel.name == "Rich");
      }
      if (this.$route.params.category == "lg") {
        return this.schedule.find(schel => schel.name == "LG");
      }
       if (this.$route.params.category == "bonanza") {
        return this.schedule.find(schel => schel.name == "Bonanza");
      }
      if (this.$route.params.category == "fortune") {
        return this.schedule.find(schel => schel.name == "Fortune");
      }
      if (this.$route.params.category == "midweek") {
        return this.schedule.find(schel => schel.name == "MidWeek");
      }
      if (this.$route.params.category == "msp") {
        return this.schedule.find(schel => schel.name == "MSP");
      }
      if (this.$route.params.category == "national") {
        return this.schedule.find(schel => schel.name == "National");
      }
      if (this.$route.params.category == "cash") {
        return this.schedule.find(schel => schel.name == "Cash");
      }
      if (this.$route.params.category == "winna") {
        return this.schedule.find(schel => schel.name == "Winna");
      }
      if (this.$route.params.category == "millim") {
        return this.schedule.find(schel => schel.name == "Millions");
      }
    }
  }
};
</script>
<style>
.submit {
  margin-top: 20px;
}
.balance2 {
  height: 20px;
  margin-top: 13px;
  margin-left: 10px;
}
input {
  padding-left: 20px !important;
}
.pill-green {
  background: #269a4c !important;
  margin-left: 5px;
  padding: 2px 15px;
  border-radius: 5px;
}
.direct-pills ul {
  border: none !important;
  flex-direction: column;
}
.direct-pills li {
  padding: 8px 0;
  font-weight: bold;
}
.pill-default {
  border: 1.5px solid #e8e8e8 !important;
  margin-left: 5px;
